import { redirect } from "next/navigation"; // Next.js v13+ unterstützt redirect auf dem Server
import prisma from "../../../prisma/db";

export default async function WorkshopDetail({
  params,
}: {
  params: { id: string };
}) {
  const { id } = params;
  const workshop = await prisma.workshop.findUnique({
    where: { id: parseInt(id, 10) },
    include: {
      Verein: true, // Verein-Daten mit einbeziehen
    },
  });

  const instagramUrl =
    workshop?.instagramPostUrl ||
    workshop?.Verein?.instagramProfileUrl ||
    "https://instagram.com"; // Fallback auf Instagram-Profil

  redirect(instagramUrl); // Führt eine Server-seitige Weiterleitung durch
}
